@charset "UTF-8";

@import "base";

.wrap{background: #F4F5F8;}
.wrap.bg-white{background: #fff;}

.contents{position: relative; margin: 0 auto; padding: 0 48px 120px; max-width: 1416px; height: 100%;}
.contents.full{margin-bottom: 0; min-height: calc(100vh - 80px);}
.contents.full .content-box{padding-bottom: 40px;}

.content-box{margin-top: 60px; padding: 24px; background: $c-white; border-radius: 24px; box-shadow: 0 3px 6px 0 rgba(0, 37, 106, 0.05);}
.content-box.pd-l{padding: 40px;}
.content-box.pd-m{padding: 32px 40px;}
.content-box.pd-s{padding: 24px 88px;}
.content-box.pd-xs{padding: 0;}

.content-box.bg-none{background-color: transparent; box-shadow: none;}

/*
* header style
 */
.header{height: 80px; border-bottom: 1px solid #D8DDE2;}
.header .inner{display: flex; align-items: center; margin: 0 auto; padding: 0 48px; max-width: 1416px; height: 100%;}

.header .header-menu{display: flex; align-items: center; flex-shrink: 0;}
.header .header-menu h1{margin-right: 32px; width: 152px;}
.header .header-menu .mob-menu{display: none;}

.header .header-con{display: flex; align-items: center; width: 100%;}
.header .header-util{display: flex; align-items: center; margin-left: auto;}
.header .header-util .mypage{display: flex; align-items: center;}
.header .header-util .mypage::after{content:""; margin: 0 24px; display: block; width: 1px; height: 16px; background-color: #D8DDE2;}
.header .header-util .service-list{display: flex; align-items: center;}
.header .header-util .service-list li + li{margin-left: 6px;}
.header .header-util .service-list li a{display: block; padding: 5px 16px; border: 1px solid #B9C0C6; border-radius: 24px;}

.nav .gnb-list{display: flex; justify-content: center; align-items: center;}
.nav .gnb-list li a{display: block; padding: 12px 24px; -webkit-border-radius: 12px;-moz-border-radius: 12px;border-radius: 12px;}
.nav .gnb-list li:hover a{background-color: #D2DFF9;}
.nav .gnb-list li.active a{color: #3575F6;}

.header.fixed{z-index: 1; position: absolute; left: 0; top: 0; width: 100%; background-color: rgba(0,0,0,0.15); border-bottom: none;}
.header.fixed .header-util .mypage{color: #fff;}
.header.fixed .header-util .service-list li a{color: #fff; background-color: rgba(255,255,255,0.15);}
.header.fixed .nav .gnb-list li a{color: #fff;}
.header.fixed .nav .gnb-list li.active a{color: #fff;}

.header.progress{z-index: 1; position: fixed; left: 0; top: 0; width: 100%; background-color: rgba(0,0,0,0.15); border-bottom: none;}
.header.progress .header-util .mypage{color: #fff;}
.header.progress .header-util .service-list li a{color: #fff; background-color: rgba(255,255,255,0.15);}
.header.progress .nav .gnb-list li a{color: #fff;}
.header.progress .nav .gnb-list li.active a{color: #fff;}

.mob-nav{display: none;}

.footer{padding: 40px 0 32px; background-color: #fff; border-top: 1px solid #D8DDE2;}
.footer .inner{margin: 0 auto; padding: 0 48px; max-width: 1416px; height: 100%;}

.footer-menu{display: flex; justify-content: space-between; align-items: center;}
.footer-menu .footer-logo{font-size: 24px; font-weight: 700;}
.footer-menu .footer-logo span{margin-left: 5px; font-size: 14px; color: #7C828B; font-weight: 400; vertical-align: bottom;}
.footer-menu ul{display: flex; align-items: center;}
.footer-menu ul li + li{margin-left: 48px;}
.footer-menu ul li a{font-size: 16px; font-weight: 500;}

.contact-con{display: flex; align-items: center; justify-content: space-between; padding: 20px 0; border-bottom: 1px solid #D8DDE2;}
.contact-con .info-con{}
.contact-con .info-con dl{display: flex; align-items: center; line-height: 1.6;}
.contact-con .info-con dl dt{width: 50px; font-size: 14px; color: #7C828B;}
.contact-con .info-con dl dd{font-size: 14px;}
.contact-con .info-con dl:first-of-type dd{color: #3575F6;}

.address-con{position: relative; padding-top: 20px;}
.address-con .copy{font-size: 16px; font-weight: 700;}

.address-con address{margin: 12px 0;}
.address-con address div{display: flex; align-items: center; flex-wrap: wrap;}
.address-con address div p{display: flex; align-items: center; font-size: 12px; color: #7C828B; line-height: 1.6;}
.address-con address div p::after{content:""; margin: 0 12px; display: block; width: 1px; height: 12px; background-color: #D8DDE2;}
.address-con address div p:last-of-type::after{display: none;}

.address-con .terms-list{display: flex; align-items: center;}
.address-con .terms-list li{font-size: 14px; font-weight: 500;}
.address-con .terms-list li + li{margin-left: 27px;}

.address-con .share-list{display: flex; align-items: center; position: absolute; right: 0; top: 20px;}
.address-con .share-list li{margin-left: 12px; width: 40px;}
.address-con .share-list li a{display: block; width: 100%; height: 100%; border: 1px solid #D8DDE2; border-radius: 50%;}

.dim{z-index: 100; position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.4);}

.pop-wrap{overflow: hidden auto; z-index: 9999; display: none; align-items: center; justify-content: center; position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.5);}
.pop-wrap.open{display: flex;}
.pop-inner{overflow: hidden; position: relative; background-color: #fff; -webkit-border-radius: 20px; -moz-border-radius: 20px; border-radius: 20px; box-shadow: 0 10px 15px rgba(0,0,0,0.1);}
.pop-inner::before{content:""; display: block; width: 100%; height: 100%;}
.pop-inner.w496{width: 496px;}
.pop-inner .close-btn{position: absolute; right: 24px; top: 24px; width: 24px;}

.pop-inner .info-con{padding: 32px 0 48px; text-align: center;}
.pop-inner .info-con .title{margin-bottom: 12px; font-size: 24px; font-weight: 700;}
.pop-inner .info-con .gray-box{margin: 0 40px 16px; padding: 20px 32px; background-color: #F4F5F8; border-radius: 12px;}
.pop-inner .info-con .gray-box dl{display: flex; margin-bottom: 8px; font-size: 14px;}
.pop-inner .info-con .gray-box dl:last-child{margin-bottom: 0;}
.pop-inner .info-con .gray-box dt{flex-shrink: 0; margin-right: 12px; min-width: 60px; color: #7C828B; text-align: left;}
.pop-inner .info-con .gray-box dd{font-weight: 500; color: #2E3337;}
.pop-inner .info-con .txt{font-size: 16px; color: #7C828B; word-break: keep-all;}
.pop-inner .info-con .txt span{font-weight: 500;}
.pop-inner .info-con .page-btn{padding: 32px 0 0 0;}
.pop-inner .info-con .page-btn .btn{width: 220px;}

//pop-video
.pop-wrap.pop-video{flex-direction: column;}
.pop-wrap.pop-video .pop-inner{border-radius: 0; width: 700px;}
.pop-wrap.pop-video .pop-inner::before{display: none;}
.pop-video .video{position: relative; padding-bottom: 56.25%; height: 0; overflow: hidden; max-width: 100%;}
.pop-video .video iframe, .pop-video .video object, .pop-video .video embed {position: absolute; top: 0; left: 0; width: 100%; height: 100%;}
.pop-video .close-btn{margin-top: 20px; width: 60px; height: 60px; border: 1px solid #707070; border-radius: 50%; box-shadow: 0 5px 12px rgba(0, 0, 0, 0.3);}

.aside{width: 224px; flex-shrink: 0;}
.aside .navi{}
.aside .navi .main-link > li{margin-bottom: 20px;}
.aside .navi .main-link > li > a{display: block; padding: 0 20px; height: 40px; line-height: 40px; font-size: 16px; font-weight: 700; -webkit-border-radius: 8px;-moz-border-radius: 8px;border-radius: 8px;}
.aside .navi .main-link > li.focus > a{color: $c-white; background-color: $c-blue;}
.aside .navi .sub-link{margin-top: 4px;}
.aside .navi .sub-link > li{padding: 0 20px; height: 32px; line-height: 32px;}
.aside .navi .sub-link > li > a{font-size: 14px; color: #7C828B;}
.aside .navi .focus .sub-link > li.active > a{color: $c-blue;}
.aside .btn-con{padding: 20px 20px 0; border-top: 1px solid #D8DDE2;}
.aside .btn-con a{font-size: 14px; color: #7C828B;}

// progress-container
.progress-container{width: 100%; height: 8px; background-color: #D8DDE2;}
.progress-container .progress-bar{width: 0; height: 100%; background-color: #3575F6;}

.progress{-ms-overflow-style: none; /* 인터넷 익스플로러 */ scrollbar-width: none; /* 파이어폭스 */}
.progress::-webkit-scrollbar { display: none; }

@media all and (max-width:1400px){
    .header .header-menu h1{margin-right: 24px; width: 140px;}

    .header .header-util .mypage::after{margin: 0 18px; height: 14px;}
    .header .header-util .service-list li a{padding: 4px 14px; border-radius: 20px;}
    .nav .gnb-list li a{padding: 12px 18px;}
}

@media all and (max-width:1300px){
    .header .header-menu h1{margin-right: 20px;}

    .header .header-util .mypage::after{margin: 0 15px; height: 14px;}
    .nav .gnb-list li a{padding: 12px 15px;}
}

@media all and (max-width:1200px){
    .header .header-menu h1{margin-right: 16px; width: 128px;}

    .header .header-util .mypage{font-size: 15px;}
    .header .header-util .mypage::after{margin: 0 12px; height: 12px;}
    .header .header-util .service-list li a{padding: 3px 10px; font-size: 15px; border-radius: 16px;}
    .nav .gnb-list li a{padding: 12px 12px; font-size: 15px;}

    .aside{width: 180px;}
    .aside .navi .main-link > li > a{padding: 0 16px; font-size: 14px;}
    .aside .navi .sub-link > li{padding: 0 16px;}
    .aside .navi .sub-link > li > a{font-size: 12px;}
    .aside .btn-con{padding: 20px 16px 0;}
    .aside .btn-con a{font-size: 12px;}
}

@media all and (max-width:1100px){
    .header .header-menu h1{width: 120px;}
    .header .header-util .mypage::after{margin: 0 10px;}
    .nav .gnb-list li a{padding: 12px 10px;}

    .address-con address div p:nth-of-type(2n)::after{display: none;}
    .address-con address div p:last-of-type{width: 100%;}
}

@media all and (max-width:960px){
    .wrap{padding-top: 60px; background: #fff;}
    .wrap.bg-color{background: #F4F5F8;}
    .wrap.bg-color .header{background: #F4F5F8;}

    .content-box, .content-box.pd-l, .content-box.pd-m, .content-box.pd-s{padding: 0; box-shadow: none;}

    .header{z-index: 100; position: fixed; left: 0; top: 0; width: 100%; height: 60px; background-color: #fff;}
    .header .header-menu{width: 100%; justify-content: space-between;}
    .header .header-menu .mob-menu{display: flex; align-items: center;}
    .header .header-menu .mob-menu .mob-btn{margin-right: -12px; padding: 12px; width: 48px;}
    .header .header-menu .mob-menu .mypage{margin-right: 10px;}
    .header .header-menu .mob-menu .mypage a{display: block;}
    .header .header-menu .mob-menu .mypage .login-btn{padding: 4px 10px; font-size: 14px; border: 1px solid #B9C0C6; -webkit-border-radius: 20px;-moz-border-radius: 20px;border-radius: 20px;}
    .header .header-menu .mob-menu .mypage .mypage-btn{margin-right: -12px; padding: 12px; width: 48px;}

    .header .header-con{display: none;}

    .header.fixed{z-index: 100; position: fixed;}
    .header.fixed .header-menu .mob-menu .mypage .login-btn{color: #fff;}

    .header.progress .header-menu .mob-menu .mypage .login-btn{color: #fff;}

    .mob-nav{z-index: 1000; display: block; position: fixed; left: -100%; top: 0; width: 100%; height: 100vh; background-color: #fff;}
    .mob-nav .header-menu{display: flex; align-items: center; padding: 0 24px; width: 100%; height: 60px; justify-content: space-between; border-bottom: 1px solid #D8DDE2;}
    .mob-nav .header-menu .logo{width: 120px;}
    .mob-nav .header-menu .mob-menu{display: flex; align-items: center;}
    .mob-nav .header-menu .mob-menu .close-btn{margin-right: -12px; padding: 12px; width: 48px;}
    .mob-nav .header-menu .mob-menu .mypage{margin-right: 10px;}
    .mob-nav .header-menu .mob-menu .mypage a{display: block;}
    .mob-nav .header-menu .mob-menu .mypage .login-btn{padding: 4px 10px; font-size: 14px; border: 1px solid #B9C0C6; -webkit-border-radius: 20px;-moz-border-radius: 20px;border-radius: 20px;}
    .mob-nav .header-menu .mob-menu .mypage .mypage-btn{margin-right: -12px; padding: 12px; width: 48px;}

    .mob-nav .nav-list{overflow: hidden auto; padding: 0 24px; height: calc(100% - 60px);}
    .mob-nav .nav-list .gnb-list{padding: 16px 0;}
    .mob-nav .nav-list .gnb-list + .gnb-list{border-top: 1px solid #D8DDE2;}
    .mob-nav .nav-list .gnb-list li a{display: block; padding: 16px 0; font-size: 16px;}

    .footer-menu ul li + li{margin-left: 30px;}

    .pop-wrap.pop-video{padding: 0 48px;}
    .pop-wrap.pop-video .pop-inner{width: 100%;}

    .aside{position: relative; margin-bottom: 32px; width: 100%; background-color: #fff; border-bottom: 1px solid #D8DDE2;}
    .aside .navi .main-link{overflow-x: auto; font-size: 0; text-align: center; white-space: nowrap;}
    .aside .navi .main-link > li{display: inline-block; margin-bottom: 0;}
    .aside .navi .main-link > li > a{position: relative; padding: 12px 20px; height: auto; line-height: 1; font-size: 16px; color: #7C828B; white-space: nowrap; font-weight: 400; -webkit-border-radius: 0;-moz-border-radius: 0;border-radius: 0;}
    .aside .navi .main-link > li.focus > a{background-color: $c-white; color: #141618;}
    .aside .navi .main-link > li.focus > a::after{content:""; position: absolute; left: 50%; bottom: 0; transform: translateX(-50%); width: calc(100% - 40px); height: 2px; background-color: $c-blue;}

    .aside .navi .sub-link{display: none; margin-top: 0;}
    .aside .navi .focus .sub-link{display: flex; position: absolute; left: 50%; transform: translateX(-50%); top: 100%; margin-top: 6px;}
    .aside .navi .focus .sub-link > li{display: flex; align-items: center; padding: 0; height: auto; line-height: 1;}
    .aside .navi .focus .sub-link > li > a{display: block; padding: 0 12px; font-size: 14px; line-height: 36px;}
    .aside .navi .focus .sub-link > li::after{content:""; display: block; width: 1px; height: 14px; background-color: #D8DDE2;}
    .aside .navi .focus .sub-link > li:last-of-type::after{display: none;}

    .aside .btn-con{display: none;}

    .progress-container{height: 6px;}
}

@media all and (max-width:720px) {
    .wrap{padding-top: 16.67vw;}

    .inner {padding: 0 6.67vw;}
    .contents{padding: 0 6.67vw 16.67vw;}
    .content-box{margin-top: 8.89vw;}
    .contents.full{min-height: calc(100vh - 16.67vw);}
    .contents.full .content-box{padding-bottom: 0;}

    .header {height: 16.67vw;}
    .header .inner{ padding: 0 6.67vw;}
    .header .header-menu h1 {margin-right: 0; width: 29.44vw;}
    .header .header-menu .mob-menu .mob-btn{margin-right: -3.33vw; padding: 3.33vw; width: 13.33vw;}
    .header .header-menu .mob-menu .mypage{margin-right: 2.78vw;}
