    .mob-nav .header-menu .mob-menu .close-btn{margin-right: -3.33vw; padding: 3.33vw; width: 13.33vw;}
    .mob-nav .header-menu .mob-menu .mypage{margin-right: 2.78vw;}
    .mob-nav .header-menu .mob-menu .mypage .login-btn{padding: 1.11vw 2.78vw; font-size: 3.89vw; border: 0.28vw solid #B9C0C6; -webkit-border-radius: 5.56vw;-moz-border-radius: 5.56vw;border-radius: 5.56vw;}
