    .footer{padding: 11.11vw 0 8.89vw;}
    .footer .inner{ padding: 0 6.67vw;}

    .footer-menu{display: block;}
    .footer-menu .footer-logo{font-size: 5.56vw;}
    .footer-menu .footer-logo span{margin-left: 0; font-size: 3.33vw;}
