    .aside .navi .focus .sub-link > li::after{width: 0.28vw; height: 3.89vw;}

    .progress-container{height: 1.67vw;}
}

.employ-wrap .key-visual{overflow: hidden; position: relative; width: 100%; height: 200px;}
.employ-wrap .key-visual img{position: relative; left: 50%; transform: translateX(-50%); min-width: 1920px;}
.employ-wrap .key-visual .mo_img{display: none;}

.detail-wrap .key-visual{overflow: hidden; position: relative; width: 100%; height: 200px;}
.detail-wrap .key-visual.large{height: 420px;}
.detail-wrap .key-visual img{position: relative; left: 50%; transform: translateX(-50%); min-width: 1920px;}
.detail-wrap .key-visual .mo_img{display: none;}

.detail-wrap .key-visual .txt-box{position: absolute; bottom: 40px; left: 50%; transform: translateX(-50%); width: 100%; color: #fff; text-align: center; z-index: 1;}
.detail-wrap .key-visual .txt-box .sub-txt{margin-bottom: 6px; font-size: 16px;}
.detail-wrap .key-visual .txt-box .tit{font-size: 40px; font-weight: 700;}
.detail-wrap .key-visual .txt-box .desc-txt{margin-top: 24px; font-size: 16px; color: #D8DDE2;}
.detail-wrap .key-visual .txt-box .desc-txt span::after{content: ''; display: inline-block; margin: -3px 12px 0; width: 1px; height: 16px; background-color: #D8DDE2; vertical-align: middle;}
.detail-wrap .key-visual .txt-box .desc-txt span:last-child::after{display: none;}

@media all and (max-width:960px) {
    .employ-wrap{margin-top: -60px;}
    .employ-wrap .key-visual img{left: 30%;}
    .detail-wrap{margin-top: -60px;}
